.component-1-icon {
  position: absolute;
  top: 80px;
  left: 92px;
  width: 12px;
  height: 12px;
}
.search,
.searchicon {
  position: relative;
  width: 15px;
  height: 18px;
}
.search {
  font-weight: 600;
  display: inline-block;
  width: 85px;
  flex-shrink: 0;
  opacity: 0;
}
.searchbar {
  position: absolute;
  top: 72px;
  left: 239px;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro-100);
  width: 31px;
  height: 29px;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-3xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.component-1-parent {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  height: 216px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--color-black);
  font-family: var(--font-inter);
}
